






































































































































































































































































.CodeMirror {
	font-size: 16px;
	height: 76vh;
	z-index: 0;
}

#tree {
	.panel-body {
		overflow: hidden;
	}
	.el-tree {
		overflow-x: scroll;
		height: 76vh;
		border: none;
		div[role='treeitem'] {
			.el-tree-node__content {
				height: 35px;
			}
		}
	}
}

.el-tree-node.is-current {
	background: #EEE;
}
